.contactWrapper {
  /* margin-top: 1000px; */
  /* min-height might need to change */
  height: 70vh;
  width: 100%;
  text-align: center;
  justify-content: center;
  display: flex;
  align-items: center;
  color: white;
}

.contactContainer {
  text-align: center;
  width: 100%;
  min-height: auto;
  justify-content: center;
  display: flex;
  align-items: center;
}

.contactRow {
  text-align: center;
  width: 100%;
  min-height: auto;
  justify-content: center;
  display: flex;
  align-items: center;
}

.contactCol {
  text-align: center;
  width: 100%;
  min-height: auto;
  justify-content: center;
  display: flex;
  align-items: center;
}

.loaderRow {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.loaderCol {
  display: flex;
  /* justify-content: center; */
  /* align-items: center; */
  /* min-height: 100vh; */
  overflow: hidden;
}

.loader {
  position: relative;
  padding: 60px 100px;
  transform: skewY(-15deg) translateX(50px) translateY(50px);
  animation: animateColor 2.5s linear infinite;
}

@keyframes animateColor {
  0% {
    filter: hue-rotate(0deg);
  }
  100% {
    filter: hue-rotate(360deg);
  }
}

.loader::before {
  content: "";
  position: absolute;
  top: 150px;
  width: 200px;
  height: 100px;
  background: rgba(0, 0, 0, 0.1);
  transform-origin: bottom;
  transform: skewX(45deg);
  filter: blur(20px);
}

.span {
  position: relative;
  width: 100px;
  height: 20px;
  display: block;
  background: hsl(212, 6%, 26%);
  transition: 0.5s;
  animation: animate 5s ease-in-out infinite;
}

@keyframes animate{
  0%, 100% {
    transform: translateX(-20px);
  }
  50% {
    transform: translateX(20px);
  }
}

.span:hover {
  background: hsl(204, 85%, 57%);
  transition: 0s;
}

.span::before {
  content: "";
  position: absolute;
  top: 0;
  left: -75px;
  height: 100%;
  width: 75px;
  background: hsl(204, 5%, 19%);
  transform-origin: right;
  transform: skewY(45deg);
  transition: 0.5s;
}

.span:hover::before {
  background: hsl(205, 59%, 30%);
  transition: 0s;
}

.span::after {
  content: "";
  position: absolute;
  left: 0;
  top: -75px;
  width: 100%;
  height: 75px;
  transform-origin: bottom;
  transform: skewX(45deg);
  background: hsl(220, 8%, 23%);
  transition: 0.5s;
}

.span:hover::after {
  background: hsl(203, 64%, 44%);
  transition: 0s;
}

